import React from 'react';
import {Button} from 'reactstrap';

class Connexion extends React.Component  {

    constructor(props){
        super(props);
        this.state = {
            identifiant : 'zdiawara',
            password : 'secret'
        };
    }

    handle = ({target})=>{
        this.setState(()=>{
            let newState = {};
            newState[target.name] = target.value;
            return newState;
        })
    }

    onLogin = ()=>{
        this.props.login(this.state.identifiant,this.state.password)
    }

    render(){
        return (
            <div className="login-container">
                <div className="login">
                    <h1>Authentification</h1>
                    <hr />
                    <div className="alert alert-primary">Identifiez-vous</div>
                    <div className="form-group">
                        <input 
                            name="identifiant"
                            type="text" 
                            className="form-control login-form" 
                            placeholder="Votre identifiant" 
                            disabled={this.props.isLogging} 
                            value={this.state.identifiant}
                            onChange={this.handle}/>
                        <input 
                            type="password" 
                            name="password"
                            className="form-control login-form" 
                            placeholder="Mot de passe"
                            disabled={this.props.isLogging}
                            value={this.state.password}
                            onChange={this.handle}
                        />
                    </div>
                    <div className="login-footer">
                        <Button  disabled={this.props.isLogging} onClick={this.onLogin} className="btn btn-secondary btn-block login-btn">Connexion</Button>
                        <a className="login-reset" href="/">Mot de passe oubli ?</a>
                    </div>
                </div>
            </div>
        );
    }

}


export default Connexion;