import { CLASSE_EXAMEN } from "./constants";
import { CODE_EXAMENS } from "../routes/liens";
import marked from 'marked-katex';
import { toRoman } from "./toroman";

export function formatProgramme(programme){
  //| Par ${teacher.firstname} ${teacher.lastname}
  if(programme){
    const {classe,specialite,teacher} = programme;
    return `${specialite.code} ${classe.abreviation}`
  }
  return "";
}
export function getControleTitle(codeClasse, typeClasse, typeControle, year,i=""){
  if(typeControle===CODE_EXAMENS && isClasseExamen(typeClasse)){
      if(codeClasse==="troisieme"){
          return `BEPC ${year}`;
      }
      return `BAC ${year}`;
  }
  return `Sujet de devoirs ${i}`;
}

export function diseable(page,type=null){
  return page===CODE_EXAMENS && !isClasseExamen(type);
}

export function isClasseExamen(type=null){
  return type !== null && type.code === CLASSE_EXAMEN;
}

export function isStudent(role){
  if(!role){
    return false;
  }
  return role.code === "student";
}

export function isTeacher(role){
  if(!role){
    return false;
  }
  return role.code === "teacher";
}

export function getTableMatieres(content){
  if(content===null){
      return [];
  }
  let contents = [];
  let contentHtml = document.createElement("div");
  
  contentHtml.innerHTML = marked(content);
  let hs = contentHtml.querySelectorAll("h1, h2");

  let index = -1;
  let subIndex = 1;
  for(let i=0; i < hs.length; i++){
      let numero = null;
      if(hs[i].tagName==="H1"){
          index = index + 1;
          numero = toRoman(index + 1);
          contents.push({
              id : hs[i].id,
              titre : hs[i].innerText,
              subSections : []
          });
          subIndex = 1;
      }else{
          numero = toRoman(index + 1)+"."+subIndex;
          contents[index] && contents[index].subSections.push({
              titre : hs[i].innerText,
              id : hs[i].getAttribute("id")
          });
          subIndex++;
      }
      hs[i].setAttribute("data-numero",  numero);
  }
  return {contents,contentHtml: contentHtml.innerHTML};
}