import update from 'react-addons-update';


const initialState = {
    appSpecialites : [],
    appSpecialiteSelected: "0",
    appSpecialitesIsLoading: true
}

export default function reducer(state = initialState, action = {}) {

    switch (action.type) {

        case "SET_APP_SPECIALITES":
            return update(state, {
                appSpecialites: {
                    $set: action.specialites
                }
            });

        
        case "SET_APP_SPECIALITE_SELECTED":
            return update(state, {
                appSpecialiteSelected: {
                    $set: action.codeSpecialite
                }
            });

        case "SET_APP_SPECIALITES_LOADING":
            return update(state, {
                appSpecialitesIsLoading: {
                    $set: action.loading
                }
            });
        
        default:
            return state;
    }
}