//import update from 'react-addons-update';


const initialState = {
    chapitres : [],
    examens : [],
    loadingChapitres: false,
    loadingExamens: true
}

export default function reducer(state = initialState, action = {}) {

    switch (action.type) {

        case "LOADING_EXAMENS_RECENTS":
            return {...state, loadingExamens : action.loading};
        case "LOADING_CHAPITRES_RECENTS":
            return {...state, loadingChapitres : action.loading};
        
        case "SET_CHAPITRES_RECENTS":
            return {...state, chapitres : action.data};

        case "SET_EXAMENS_RECENTS":
            return {...state, examens : action.data};

        default:
            return state;
    }
}

export const chapitresRecents = (state) => state.recentReducer.chapitres;
export const examensRecents = (state) => state.recentReducer.examens;

export const getLoadingChapitresRecents = (state) => state.recentReducer.loadingChapitres;
export const getLoadingExamensRecents = (state) => state.recentReducer.loadingExamens;
