const initialState = {
    user: null,
    authenError : false,
    logged: false,
    logging: false,
    loadingMe : true,
}

export default function reducer(state = initialState, action = {}) {

    switch (action.type) {

        case "SET_USER":
            return {...state,user: action.user}

        case "AUTHEN_ERROR":
            return{...state,authenError : action.error}

        case "LOGGED":
            return{...state,logged : action.logged}

        case "LOGGING":
            return{...state,logging : action.logging}

        case "LOADING_ME":
            return {...state, loadingMe : action.loading}

        default:
            return state;
    }
}

export const isLogging = (state)=>(state.userReducer.logging);
export const getUser = (state)=>(state.userReducer.user);

export const loadingMe = (state)=>(state.userReducer.loadingMe);
export const isAuthenError = (state)=>(state.userReducer.authenError);

export const isOpenAuth = (state)=>(state.userReducer.isOpenAuth);