import React from 'react';
import {Link} from 'react-router-dom';
//import { getLienEnseignement } from '../../../navigation';
import TextRender from '../../../commun/TextRender';

import EnseignementAside from '../../../commun/EnseignementAside';
import { editerContent, editer } from '../../../navigation';
import { CODE_LIVRES } from '../../../routes/liens';

class ViewBook extends React.Component  {

    componentDidMount(){
        window.scroll(0,0);
        this.props.loadBook(this.props.match.params.bookID);
    }


    render(){

        if(!this.props.book.id){
            return(
                <div className="container">
                    
                </div>
            )
        }

        return (      
            <>
                <div className="enseignement-menu__container">
                    <div className="container">
                        <div className="enseignement-menu clearfix">
                            <div className="float-left d-flex align-items-center">
                                <Link to = {`/${CODE_LIVRES}`} className="btn btn-white enseignement-menu__return">
                                    <img alt="" src={require('../../../img/back.png')} />
                                    <span>Retour</span>
                                </Link>
                                <h1 className="enseignement-menu__title">{this.props.book.title}</h1>
                            </div>
                            <div className="float-right p-0">
                                <div className="btn btn-vert">Publier</div>
                                <div className="btn btn-rouge ml-2">Supprimer</div>
                            </div>
                        </div>
                    </div>
                </div>
                
                <div className="container">
                    <div className="enseignement-container">
                        <EnseignementAside image = {require('../../../img/maths-image.png')} title = "Table des matires">
                            {this.props.book.plan.map((content,i)=>(
                                <div key={"c"+content.id+i}>
                                    <a className="principal" href={`#${content.id}`}>
                                        {content.titre}
                                    </a>
                                    {content.subSections.map((sub,i)=>(
                                        <a key={sub.titre+i} className="sub" href={`#${sub.id}`}>
                                            {sub.titre}
                                        </a>
                                    ))}
                                </div>
                            ))}
                        </EnseignementAside>
                        <main id="enseignement-main" className="enseignement-main" >
                            <div className="enseignement-main__presentation">
                                <div className="enseignement-main__header">
                                    <div className="enseignement-main__category">
                                        {this.props.book.category.name} | {this.props.book.author.matiere.name}
                                    </div>
                                    <div className="enseignement-main__price">
                                        {this.props.book.price} Frs
                                    </div>
                                    <div className="enseignement-main__teacher">
                                        Par {this.props.book.author.firstname} {this.props.book.author.lastname}
                                    </div>
                                </div>
                                <div className="enseignement-main__resume">
                                    <TextRender
                                        isHtml = {false}
                                        text = {this.props.book.resume}/>
                                </div>
                                <p className="enseignement-main__classes">
                                    {this.props.book.classe_options.map(({classe,option},i)=>(
                                        <span key={i+classe.code}>
                                            {classe.name} {option && option.name} {this.props.book.classe_options.length-1!==i && ' | '}
                                        </span>
                                    ))}
                                </p>
                                <div className="clearfix">
                                    <div className="float-right enseignement-main__teacher">
                                        {this.props.book.teachers.length !== 0 && "Ralis Avec "}
                                        {this.props.book.teachers.map((teacher,i)=>(
                                            <span key={teacher.username}>
                                                {teacher.lastname[0]} . {teacher.firstname} {this.props.book.teachers.length-1!==i && ' | '}
                                            </span>
                                        ))}
                                    </div>
                                    <Link to={editer(CODE_LIVRES,this.props.book.id)} className="float-left btn btn-sm btn-rouge">
                                        Modifier le livre
                                    </Link>
                                </div>
                                {/*<div className="text-right">
                                    <div className="btn btn-rouge mb-2">
                                        Modifier
                                    </div>
                                    </div>*/}
                            </div>
                            <hr />
                            <div className="enseignement-main__content">
                                <div className="text-right">
                                    <Link to={editerContent(CODE_LIVRES,this.props.book.id)} className="btn btn-sm btn-rouge">
                                        Modifier le contenu
                                    </Link>
                                </div>
                                <TextRender
                                    text = {this.props.book.content.data}/>
                            </div>
                        </main>
                    </div>
                </div>
            </>
        );
    }

}


export default ViewBook;