import React from 'react'
import { Switch, Route } from 'react-router-dom';
import {connect} from 'react-redux';
//import EditerResume from '../../pages/Cours/EditerCours/EditerResume';
import { CODE_LIVRES } from '../routes/liens';
import { setAppPage } from '../redux/actions/appActions';
import ListLivres from '../pages/Livres/List';
import ViewBook from '../pages/Livres/View';
import EditBook from '../pages/Livres/Edit';


class LivresLayout extends React.Component{
    componentDidMount(){
        this.props.setAppPage(CODE_LIVRES);
    }
    render(){
        return(
            <>
                <Switch>
                    <Route exact path={`${this.props.match.url}`} component={ListLivres} />
                    <Route exact path={`${this.props.match.url}/lecture/:bookID`} component={ViewBook} />
                    <Route exact path={`${this.props.match.url}/editer`} component={EditBook} />
                </Switch>
            </>
        );
    }
}

function mapStateToProps(state) {
    return {};
}

function mapDispatchToProps(dispatch) {
    return {
        setAppPage : function(page){
            dispatch(setAppPage(page));
        }
    }
}

export default connect(mapStateToProps,mapDispatchToProps)(LivresLayout);