import {connect} from 'react-redux';

import Chapitre from './Chapitre.jsx';

import { loadChapitre} from '../../../../redux/actions/chapitresActions.js';

import {getFullChapitre,isLoadingChapitre} from '../../../../redux/reducers/chapitreReducer';

function mapStateToProps(state) {
    return {
        chapitre : getFullChapitre(state),
        isLoadingChapitre : isLoadingChapitre(state)
    };
}

function mapDispatchToProps(dispatch) {
    return {
        loadChapitre: function(chapitreID){
            dispatch(loadChapitre(chapitreID));
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Chapitre);
