const initialState = {
    
    devoirs : [],
    devoirPageNumber : 1,
    devoirPageTotal : 0,

    examens : [],
    examenPageNumber : 1,
    examenPageTotal : 0,
    loadingControles : false,

    controle : {},
    loadingControle : false,

}

export default function reducer(state = initialState, action = {}) {

    switch (action.type) {

        
        // List devoirs
        case "SET_DEVOIRS":
            return {...state, devoirs : action.controles };
        case "SET_CONTROLE_PAGE_NUMBER":
            if(action.data.isDevoir){
                return {...state, devoirPageNumber : action.data.pageNumber };
            }
            return {...state, examenPageNumber : action.data.pageNumber};
        case "SET_CONTROLE_PAGE_TOTAL":
            if(action.data.isDevoir){
                return {...state, devoirPageTotal : action.data.pageTotal };
            }
            return {...state, examenPageTotal : action.data.pageTotal };
        // List examens
        case "SET_EXAMENS":
            return {...state, examens : action.controles };
        case "LOADING_CONTROLES":
            return {...state, loadingControles : action.loading };

        // Controle
        case "SET_CONTROLE":
            return {...state, controle : action.controle};
        case "LOADING_CONTROLE":
            return {...state, loadingControle : action.loading };

        default:
            return state;
    }
}

export const getDevoirs = ({controleReducer})=>(controleReducer.devoirs);
export const getExamens = ({controleReducer})=>(controleReducer.examens);
export const isLoadingControles = ({controleReducer})=>(controleReducer.loadingControles);
export const getDevoirPageNumber = ({controleReducer})=>(controleReducer.devoirPageNumber);
export const getDevoirPageTotal = ({controleReducer})=>(controleReducer.devoirPageTotal);
export const getExamenPageNumber = ({controleReducer})=>(controleReducer.examenPageNumber);
export const getExamenPageTotal = ({controleReducer})=>(controleReducer.examenPageTotal);

export const getFullControle = ({controleReducer})=>(controleReducer.controle);
export const isLoadingControle = ({controleReducer})=>(controleReducer.loadingControle);
