import React from 'react';
import {Link} from 'react-router-dom';
import ProtectedButton from './ProtectedButton';


const MenuRetour = ({link,title})=>(
    <div className="enseignement-menu">
        <div className="container">
            <div className="enseignement-tab">
                <Link to = {link} className="enseignement-tab__item">
                    <img alt="" className="enseignement-tab__icon" src={require('../img/back.png')} />
                    <span className="enseignement-tab__name">Retour</span>
                </Link>
                <h1 className="enseignement-tab__title">
                    {title} 
                </h1>
                <ProtectedButton icon="delete" />
            </div>
        </div>
    </div>

)

export default MenuRetour;