import {connect} from 'react-redux';

import ListCours from './ListCours';
import { loadChapitres, loadCoursClasses, setCoursClasse, setCoursEtat } from '../../../redux/actions/chapitresActions';

import {getListChapitres,isLoadingChapitres, getChapitrePageNumber, getChapitrePageTotal, getCoursClasses, getCoursEtat, getCoursClasse} from '../../../redux/reducers/chapitreReducer';

function mapStateToProps(state) {
    return {
        etat : getCoursEtat(state),
        classe : getCoursClasse(state),
        classes : getCoursClasses(state),
        chapitres : getListChapitres(state),
        loading : isLoadingChapitres(state),
        pageNumber : getChapitrePageNumber(state),
        pageTotal : getChapitrePageTotal(state)
    };
}

function mapDispatchToProps(dispatch) {
    return {
        loadChapitres: function(){
            dispatch(loadChapitres());
        },
        loadCoursClasses: function(){
            dispatch(loadCoursClasses());
        },
        setCoursClasse :  function(codeClasse){
            dispatch(setCoursClasse(codeClasse));
        },
        setCoursEtat :  function(etat){
            dispatch(setCoursEtat(etat));
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ListCours);
