import {connect} from 'react-redux';

//import {withRouter} from 'react-router-dom';

import Authentification from './Authentification';

//import {getAppClasses,getAppClasse} from '../../redux/reducers/appReducer';
//import { loadAppClasse } from '../../redux/actions/appActions';
import { login } from '../../redux/actions/userActions';
import { isLogging } from '../../redux/reducers/userReducer';

function mapStateToProps(state) {
    return {
        isLogging : isLogging(state),
    }
}

function mapDispatchToProps(dispatch) {
    return {
        login : function(identifiant,password){
            dispatch(login(identifiant,password));
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Authentification);
