import React from 'react';

class EnseignementAside extends React.Component  {

    componentDidMount(){
        const main = document.getElementById("enseignement-main");
        if(!main){
            return;
        }
        const aside = document.getElementById("enseignementAside");
        if(!aside){
            return;
        }
        window.onscroll = function() {
            if (window.pageYOffset>main.offsetTop) {
                aside.classList.add("sticky");
            } else {
                aside.classList.remove("sticky");
            }
        };
    }

    componentWillUnmount(){
        window.onscroll = null;
    }

    /**
     * Rendu de la page HTML
     */
    render(){
        return (
            <aside className="enseignement-aside">
                <div id="enseignementAside">
                    {/*<div className="enseignement-aside__image">
                        <img alt="" src={this.props.image} />
        </div>*/}
                    <div className="enseignement-aside__content">
                        <h4 className="enseignement-aside__title">{this.props.title}</h4>
                        {this.props.children}
                    </div>
                </div>
            </aside>  
        );
    }

}

export default EnseignementAside;