
export function setLoadingExercices(loadingExercices=true){
    return {type : "SET_LOADING_EXERCICES", loadingExercices}
}

export function setChapitreExercices(exercices=[]){
    return {type : "SET_CHAPITRE_EXERCICES", exercices}
}

export function setChapitreExoTotal(total=0){
    return {type : "SET_CHAPITRE_EXO_TOTAL", total}
}

export function setChapitreExoCurPage(page=0){
    return {type : "SET_CHAPITRE_EXO_CURRENT_PAGE", page}
}