import React from 'react';
import {Button} from 'reactstrap';
import { isTeacher } from '../../utils/fonctions';
import ICONS from '../../utils/images';



const ProtectedButton = (props) => {
    /*if(!isTeacher(props.user.role) || !icons[this.props.icon]){
        return null;
    }*/
    return(
        <Button onClick = {props.onClick.bind(this)} className={"icon-image "+props.className}>
            <img className={props.imageClass} src = {ICONS[props.icon]} alt = "" />
            {props.label && <span className={props.labelClass}> {props.label} </span> }
        </Button>
    )
}


ProtectedButton.defaultProps = {
    icon : 'edit',
    className : '',
    label : null,
    labelClass : '',
    imageClasse : '',
    onClick : ()=>{}
}

export default ProtectedButton;