import React from 'react'
import { Switch, Route } from 'react-router-dom';
import {connect} from 'react-redux';
//import EditerResume from '../../pages/Cours/EditerCours/EditerResume';
import { CODE_EXERCICES } from '../routes/liens';
import { setAppPage } from '../redux/actions/appActions';
import ListExercices from '../pages/Exercices/List';



class ExercicesLayout extends React.Component{
    componentDidMount(){
        this.props.setAppPage(CODE_EXERCICES);
    }
    render(){
        return(
            <>
                <Switch>
                    <Route exact path={`${this.props.match.url}`} component={ListExercices} />
                </Switch>
            </>
        );
    }
}

function mapStateToProps(state) {
    return {};
}

function mapDispatchToProps(dispatch) {
    return {
        setAppPage : function(page){
            dispatch(setAppPage(page));
        }
    }
}

export default connect(mapStateToProps,mapDispatchToProps)(ExercicesLayout);