import React from 'react';
import {Link} from 'react-router-dom';
//import { getLienEnseignement } from '../../../navigation';
import TextRender from '../../../commun/TextRender';

import EnseignementAside from '../../../commun/EnseignementAside';

class EditBook extends React.Component  {

    componentDidMount(){
        /*window.scroll(0,0);
        this.props.loadBook(this.props.match.params.bookID);*/
    }




    render(){

        return (    
            <>
                <div className="enseignement-menu__container">
                    <div className="container">
                        <div className="enseignement-menu clearfix">
                            <div className="float-left d-flex align-items-center">
                                <Link to = "/" className="btn p-0 enseignement-menu__return">
                                    <img alt="" src={require('../../../img/back.png')} />
                                    <span>Retour</span>
                                </Link>
                                <h1 className="enseignement-menu__title">Cration d'un livre</h1>
                            </div>
                            <div className="float-right btn p-0">
                                <div className="btn btn-primary">Enregistrer</div>
                            </div>
                        </div>
                    </div>
                </div>
                
                <div className="container">

                <div className="enseignement-edit__container row">
                    <main id="enseignement-main" className="enseignement-main col-9 mr-auto ml-auto" >
                        <form>
                            <div className="form-row">
                                <div className="form-group col-md-12">
                                    <label htmlhtmlFor="title">Titre du livre</label>
                                    <input type="text" className="form-control" id="title" placeholder="Renseignez le titre du livre" />
                                </div>
                            </div>
                            
                            <div className="form-row">
                                <div className="form-group col-md-4">
                                    <label htmlFor="category">Categorie</label>
                                    <select id="category" className="form-control">
                                        <option selected>Sectionnez une catgorie...</option>
                                        <option>Annale</option>
                                        <option>Livre</option>
                                    </select>
                                </div>
                                <div className="form-group col-md-4">
                                    <label htmlFor="specialite">Specialit</label>
                                    <input type="text" placeholder="Spcialit" className="form-control" id="specialite" />
                                </div>
                                <div className="form-group col-md-4">
                                    <label htmlFor="price">Prix</label>
                                    <input type="text" placeholder="Prix de vente" className="form-control" id="price" />
                                </div>
                            </div>
                            <div className="text-right">
                                <button className="btn btn-rouge">Quitter</button>
                                {' '}
                                <button className="btn btn-primary">Enregistrer</button>
                            </div>
                        </form>
                        </main>
                    </div>
                </div>
            </>
        );
    }

}


export default EditBook;