import { setToken, deleteToken} from "../../utils/token";
//import { toHome } from "../../routes/navigation";
import userService from '../../services/userService';
import classeService from '../../services/classeService';
import { toHome } from "../../navigation";
import { loadAppClasses } from "./appActions";



/**
 * 
 */
export function login(identifiant,password) {
    return function (dispatch) {
        dispatch(setLogging());
        userService.auth(identifiant, password).then((data) => {
            setToken(data.token);
            dispatch(userInfo(data.token));
            dispatch(setLogging(false));
        }).catch(({response}) => {
            dispatch(setLogging(false));
            if(!response){
                alert("Le serveur ne repond pas");
            }
        });
    }
}

export function logout(){
    return function(dispatch){
        deleteToken();
        dispatch(setUser());
    }
}

export function userInfo(token) {
    return function (dispatch) {
        dispatch(loadingMe(true));
        if(!token){
            dispatch(loadingMe(false));
            return;
        }
        userService.me(token).then(({data}) => {
            dispatch(setUser({...data, token : token}));
            dispatch(loadAppClasses());
            dispatch(loadingMe(false));
            toHome()
        }).catch(({response}) => {
            dispatch(setUser());
            dispatch(loadingMe(false));
        })
    }
}

export function toggleAuth(){
    return {type: "TOGGLE_AUTH" };
}

export function setAuthenError(error=true){
    return {type: "AUTHEN_ERROR", error};
}
export function setLogged(logged){
    return {type: "LOGGED", logged : logged};
}
export function setLogging(logging=true){
    return {type: "LOGGING", logging };
}
export function setUser(user=null){
    return {type: "SET_USER", user };
}

function loadingMe(loading=true){
    return {type: "LOADING_ME", loading };
}