import React from 'react';
import {Link} from 'react-router-dom';
import EnseignementList from '../../../commun/EnseignementList';
import EnseignementMenu from '../../../commun/EnseignementMenu';
import EnseignementCard from '../../../commun/EnseignementCard';
import { lecture, editer } from '../../../navigation';
import { CODE_LIVRES } from '../../../routes/liens';

class ListLivres extends React.Component  {

    componentDidMount(){
        this.props.loadBooks();
        //this.props.loadBooksClasses();
    }
    
    
    changeClasse = (codeClassse)=>{
        this.props.setBooksClasse(codeClassse);
        this.props.loadBooks();
    }

    changeFiltre = (etat)=>{
        this.props.setBooksEtat(etat);
        this.props.loadBooks();        
    }

    openBook = (e)=>{
        console.log(e)
    }

    /**
     * Rendu de la page HTML
     */
    render(){
        //const {specialite,classe} = this.props;
        //http://lorempixel.com/400/200/sports/${i+1}/
        //require('../../../img/maths-image.png')
        return(
            <>
                <EnseignementMenu
                    changeClasse = {this.changeClasse}
                    classes = {this.props.classes}
                    classe = {this.props.classe}
                    etat = {this.props.etat}
                    changeFiltre = {this.changeFiltre}
                    totalText = {`${this.props.pageTotal} livres trouvs`}
                >
                    <Link to = {editer(CODE_LIVRES)} className="shadow-sm btn btn-vert">
                        Ajouter un livre
                    </Link>
                </EnseignementMenu>

                <div className="container">
                    <div className="enseignement-list__container">
                        <EnseignementList>
                            {this.props.books.map(({id,resume,title,etat,author,classes},i)=>(
                                <div value = {"-1"}  key={id} className="col-md-4 col-sm-6 col-12 mb-3">
                                    <EnseignementCard
                                        title = {title}
                                        resume = {resume}
                                        etat = {etat}
                                        teacher = {author}
                                        classes = {classes}
                                        link = {lecture(CODE_LIVRES,id)}
                                        image = {<img src={require('../../../img/maths-image.png')} alt="" />}
                                    />
                                </div>                  
                            ))}
                        </EnseignementList>
                    </div>
                </div>

            </>
        );
    }
}


export default ListLivres;