const initialState = {
    
    books : [],
    pageNumber : 1,
    pageTotal : 0,

    loading : false,

    classes : [],
    classe : null,
    etat : null,

    book : {}

}

export default function reducer(state = initialState, action = {}) {

    switch (action.type) {

        // List books
        case "SET_BOOKS":
            return {...state, books : action.books };
        case "SET_BOOKS_PAGE_NUMBER":
            return {...state, pageNumber : action.pageNumber };
        case "SET_BOOKS_PAGE_TOTAL":
            return {...state, pageTotal : action.pageTotal };
        
        case "LOADING_BOOKS":
            return {...state, loading : action.loading };

        // a Book
        case "SET_BOOK" : 
            return {...state, book : action.book};

        /// Filtre 
        case "SET_BOOKS_CLASSES" : 
            return {...state, classes : action.classes}
        case "SET_BOOKS_CLASSE" : 
            return {...state, classe : action.classe}
        case "SET_BOOKS_ETAT" : 
            return {...state, etat : action.etat}

        default:
            return state;
    }
}