import {connect} from 'react-redux';

import ListLivres from './ListLivres.jsx';
import { loadBooks, loadBooksClasses, setBooksClasse, setBooksEtat } from '../../../redux/actions/booksActions.js';
import { getAppClasses } from '../../../redux/reducers/appReducer.js';


function mapStateToProps(state) {
    const {bookReducer} = state;
    return {
        etat : bookReducer.etat,
        classe : bookReducer.classe,
        classes : getAppClasses(state),
        books : bookReducer.books,
        loading : bookReducer.loading,
        pageNumber : bookReducer.pageNumber,
        pageTotal : bookReducer.pageTotal
    };
}

function mapDispatchToProps(dispatch) {
    return {
        loadBooks: function(){
            dispatch(loadBooks());
        },
        loadBooksClasses: function(){
            dispatch(loadBooksClasses());
        },
        setBooksClasse :  function(codeClasse){
            dispatch(setBooksClasse(codeClasse));
        },
        setBooksEtat :  function(etat){
            dispatch(setBooksEtat(etat));
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ListLivres);
