import React from 'react';
import {Link} from 'react-router-dom';

/*const images = {
    "maths" : require('../img/maths-card.png'),
    "pc" : require('../img/pc-card.png'),
    "biologie" : require('../img/biologie-card.png'),
    "examens" : require('../img/examens-card.png'),
    "devoirs" : require('../img/devoirs-card.png'),
}*/

const etat = {
    "nonpublie" : {
        label : "Non publi",
        className : "text-rouge"
    },
    "publie" : {
        label : "Publi",
        className : "text-vert"
    }
}
class EnseignementCard extends React.Component{

    render(){
        const {teacher,classes} = this.props;
        const key = this.props.etat.toString()==='1' ? 'publie' : 'nonpublie' ;
        return(
            <Link to={this.props.link} className="enseignement-card__container h-100 _paper">
                {this.props.image && 
                    <div className="enseignement-card__image">
                        {this.props.image}
                    </div>
                }

                <div className={"enseignement-card__header "}>
                    <div className="title-container">
                    <div className="enseignement-card__specialite">
                        {teacher.matiere.name} ({classes.map((classe,i)=>(` ${classe.abreviation} ${i!==classes.length-1?' | ' : ''} `))})
                    </div>
                    <h3 className={"enseignement-card__text"}> {this.props.title}</h3>
                    </div>
                    <div className="clearfix">
                        <div className="float-left enseignement-card__teacher">par {teacher.lastname[0]}. {teacher.firstname}</div>
                        <div className={"float-right enseignement-card__etat "+etat[key].className}>
                            {etat[key].label}
                        </div>
                    </div>
                </div>
            </Link>
        );
    }
}

export default EnseignementCard;