import React from 'react';

import {connect} from 'react-redux';

import { withRouter, Switch, Route} from 'react-router-dom'

import { userInfo } from '../redux/actions/userActions.js';
import { getToken } from '../utils/token.js';
import Authentification from '../pages/Authentification'
import { loadingMe } from '../redux/reducers/userReducer';
import AppLayout from '../layouts/AppLayout';
import AuthorizedRoute from '../AuthorizedRoute.js';
import { CODE_LIVRES, CODE_COURS, CODE_EXERCICES } from '../routes/liens.js';
import Editeur from '../commun/Editeur.jsx';


class Pages extends React.Component {

    constructor(props){
        super(props);
        props.getUserInfo();
    }

    render() {
        if(this.props.loading){
            return null;
        }
        return (
            <React.Fragment>
                <Switch>
                    <Route exact path={"/connexion"} component={Authentification} />
                    <Route exact path={"/inscription"} component={Authentification} />
                    <AuthorizedRoute exact code = {CODE_COURS} path={"/cours/gestion"} component={()=>{}} />
                    <AuthorizedRoute exact code = {CODE_EXERCICES} path={"/exercices/gestion"} component={()=>{}} />
                    <AuthorizedRoute exact code = {CODE_LIVRES} path={`/${CODE_LIVRES}/content/:contentID`} component={Editeur} />
                    <AuthorizedRoute path={"/"} component={AppLayout} />
                </Switch>
            </React.Fragment>
        )
    }
}

function mapStateToProps(state) {
    return {
        loading : loadingMe(state)
    };
}

function mapDispatchToProps(dispatch) {
    return {
        getUserInfo : function(){
            dispatch(userInfo(getToken()));
        }
    }
}

export default withRouter(connect(mapStateToProps,mapDispatchToProps)(Pages));
