import React from 'react';
import { ButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';

class ClassDropdown extends React.Component {
    constructor(props) {
    super(props);

        this.toggle = this.toggle.bind(this);
        this.state = {
            dropdownOpen: false
        };
    }

    toggle() {
        this.setState({
            dropdownOpen: !this.state.dropdownOpen
        });
    }

    onClick = (e)=>{
        this.props.onClick && this.props.onClick(e.target.value);
    }

    render() {
        const classe = this.props.classe ? this.props.classes.find((item)=>item.code===this.props.classe) : null;
        return (
            <ButtonDropdown isOpen={this.state.dropdownOpen} toggle={this.toggle}>
                <DropdownToggle color="outline-secondary" caret>
                    {classe ? classe.name : 'Classe'}
                </DropdownToggle>
                <DropdownMenu>
                    {this.props.classes.map((classe)=>(
                        <DropdownItem value = {classe.code} key={classe.code} onClick={this.onClick}>
                            {classe.name}
                        </DropdownItem>
                    ))}
                </DropdownMenu>
            </ButtonDropdown>
        );
    }
}

ClassDropdown.defaultProps = {
    classes : [],
    classe : null
}

export default ClassDropdown;