import React from 'react';
import ClassDropdown from '../ClassDropdown';
import Filtre from '../Filtre';

class EnseignementMenu extends React.Component {
    
    render() {
        return (
            <div className="enseignement-menu__container">
                <div className="container">
                    <div className="enseignement-menu clearfix">
                        <div className={`enseignement-menu__filtre float-left`}>
                            <ClassDropdown
                                onClick = {this.props.changeClasse}
                                classes = {this.props.classes}
                                classe = {this.props.classe}
                            />
                            <Filtre
                                etat = {this.props.etat}
                                onClick = {this.props.changeFiltre}
                            />
                            <h3 className="enseignement-menu__text">
                                {this.props.totalText}
                            </h3>
                        </div>
                        <div className="float-right">
                            {this.props.children}
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}


// Exporte le composant
export default EnseignementMenu;