import React from 'react';
import {Link} from 'react-router-dom';
import { CODE_LIVRES, CODE_COURS, CODE_EXERCICES, CODE_DEVOIRS, CODE_EXAMENS } from '../../routes/liens';


const enseignements = [
    {
        title : 'Mes livres',
        descriptif : 'Accdez  vos livres',
        code : CODE_LIVRES
    },{
        title : 'Mes cours',
        descriptif : 'Accdez  vos cours',
        code : CODE_COURS
    },{
        title : 'Mes exercices',
        descriptif : "Accdez  vos sujets d'exercices",
        code : CODE_EXERCICES
    },{
        title : 'Mes devoirs',
        descriptif : 'Accdez  vos sujets de devoirs',
        code : CODE_DEVOIRS
    },{
        title : 'Mes examens',
        descriptif : "Accdez  vos sujets d'examens",
        code : CODE_EXAMENS
    }
]

const ListEnseignement = ()=>(
    enseignements.map((item)=>(
        <div key={item.code} className="col-md-3 mb-3">
            <Link to={`/${item.code}`} className="card h-100 home-enseignement__card">
                <div className="card-body">
                    <h5 className="card-title text-center">{item.title}</h5>
                    <p className="card-text text-center">
                        <small className="text-muted">{item.descriptif}</small>
                    </p>
                </div>
            </Link>
        </div>
    ))
)

class Home extends React.Component  {
        
    render(){
        return (
            <div className="home">
                <div className="container">
                    <div className="row home-enseignements">
                        <div className="col-12">
                            <h1>Mes enseignements</h1>
                            <hr/>
                        </div>
                        <ListEnseignement />    
                    </div>
                </div>
            </div>
            
        );
    }

}

export default Home;