import {connect} from 'react-redux';

import {withRouter} from 'react-router-dom';

import AppLayout from './AppLayout.jsx';
import { loadAppClasses, setAppPage } from '../../redux/actions/appActions';
import {getAppClasses, appClassesLoading, getAppClasse, getAppPage, getAppSpecialite} from '../../redux/reducers/appReducer';
import { getUser} from '../../redux/reducers/userReducer.js';


function mapStateToProps(state) {
    return {
        loading : appClassesLoading(state) /*|| loadingMe(state)*/,
        classes : getAppClasses(state),
        classe : getAppClasse(state),
        specialite : getAppSpecialite(state),
        page : getAppPage(state),
        user : getUser(state)
    };
}

function mapDispatchToProps(dispatch) {
    return {
        laodClasses : function(enseignantID){
            dispatch(loadAppClasses(enseignantID));
        },
        setAppPage : function(page){
            dispatch(setAppPage(page));
        }
    }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(AppLayout));
