import jwtDecode from 'jwt-decode';


const TOKEN = "token_key";
const ROLE = "role";
const NOM = "user_lastname";
const PRENOM = "user_firstname";
const EMAIL = "user_email";


export function getToken(){
    return localStorage.getItem(TOKEN);
}

export function getUsername(){
    let username = "";
    if(isConnect()){
        try{
            username = jwtDecode(getToken()).sub;
        }catch(e){
            // TODO
        }
    }
    return username;
}

export function isConnect(){
    const token = getToken(); 
    if(token && token !== null && token.length !== 0){
        return true;
    }
    return false;
}

export function setToken(token){
    localStorage.setItem(TOKEN,token);
}

export function setRole(role){
    localStorage.setItem(ROLE,role);
}

export function getRole(){
    return localStorage.getItem(ROLE)
}

/**
 * Modifie le nom
 * @param {*} nom 
 */
export function setNom(nom){
    localStorage.setItem(NOM,nom);
}

/**
 * Rcupre le nom
 */
export function getNom(){
    return localStorage.getItem(NOM)
}


/**
 * Modifie le prnom
 * @param {*} prenom 
 */
export function setPrenom(prenom){
    localStorage.setItem(PRENOM,prenom);
}

/**
 * Rcupre le prnom
 */
export function getPrenom(){
    return localStorage.getItem(PRENOM)
}

/**
 * Modifie l'email
 * @param {*} email 
 */
export function setEmail(email){
    localStorage.setItem(EMAIL,email);
}

/**
 * Rcupre l'email
 */
export function getEmail(){
    return localStorage.getItem(EMAIL);
}

export function setUser(data={}){
    setToken(data.token);
    setRole(data.user.role);
    setPrenom(data.user.firstname);
    setNom(data.user.lastname);
    setEmail(data.user.email);
}

export function isEnseignant(){
    return getRole()==="ROLE_ENSEIGNANT";
}

export function enseignantCanWrite(username){
    return isEnseignant() && canWrite(username);
}

export function canWrite(username){
    return username === getUsername();
}


export function createAuthHeader(){
    const token = getToken();
    if(token){
        return {"Authorization": "Bearer " + token};
    }
    return {}
}

export function deleteToken(){
    localStorage.removeItem(TOKEN);
}

export function deleteUser(){
    deleteToken();
    localStorage.removeItem(ROLE);
    localStorage.removeItem(NOM);
    localStorage.removeItem(PRENOM);
    localStorage.removeItem(EMAIL);
}
