import request from './index';

const BASE_BOOKS = "/books";

function getAll(username="",codeClasse,etat,pageNumber) {
    let url = `${BASE_BOOKS}?teacher=${username}`;
    if(codeClasse){
        url = `${url}&classe=${codeClasse}`;
    }
    if(etat){
        url = `${url}&etat=${etat}`;
    }
    return request({url: `${url}&page=${pageNumber}`, method: "GET"})
}

export function getOne(bookID,full=true) {
    const url = `${BASE_BOOKS}/${bookID}?full=${full?'1':'0'}`;
    return request({url: url})
}

const bookService = {
    getAll, getOne
}

export default bookService;