import React from 'react';
import Pagination from "react-js-pagination";
//import { lienGestionChapitre } from '../../../navigation/cours';
//import { toLink } from '../../../navigation';
//import Loading from '../../../commun/Loading';
import EnseignementList from '../../../commun/EnseignementList';
import EnseignementMenu from '../../../commun/EnseignementMenu';
import EnseignementCard from '../../../commun/EnseignementCard';

class ListCours extends React.Component  {

    componentDidMount(){
        this.props.loadChapitres();
        this.props.loadCoursClasses();
    }

    changePage = (pageNumber)=>{
        const {classe,specialite} = this.props;
        const codeSpecialite = specialite ? specialite.code : null;
        this.props.loadChapitres(classe.code || '',codeSpecialite,pageNumber);
        window.scroll(0,0);
    }

    changeClasse = (codeClassse)=>{
        this.props.setCoursClasse(codeClassse);
        this.props.loadChapitres();
    }

    changeFiltre = (etat)=>{
        this.props.setCoursEtat(etat);
        this.props.loadChapitres();        
    }

    /**
     * Rendu de la page HTML
     */
    render(){
        //const {specialite,classe} = this.props;
        return(
            <>
                <EnseignementMenu
                    changeClasse = {this.changeClasse}
                    classes = {this.props.classes}
                    classe = {this.props.classe}
                    etat = {this.props.etat}
                    changeFiltre = {this.changeFiltre}
                    totalText = {`${this.props.pageTotal} cours trouvs`}
                >
                    <div className="shadow-sm btn btn-vert">
                        Ajouter un cours
                    </div>
                </EnseignementMenu>

                <EnseignementList>
                    {this.props.chapitres.map(({id,resume,title,etat,enseignement,},i)=>(
                        <div key={id} className="col-md-4 col-sm-6 col-12 mb-3">
                            <EnseignementCard
                                title = {title}
                                resume = {resume}
                                etat = {etat}
                                enseignement = {enseignement}
                                link = {"/"}
                                image = {<img src={require('../../../img/maths-image.png')} alt="" />}
                            />
                            
                        </div>                  
                    ))}
                </EnseignementList>

                <Pagination
                    activePage={this.props.pageNumber}
                    itemsCountPerPage={5}
                    totalItemsCount={this.props.pageTotal}
                    pageRangeDisplayed={5}
                    onChange={this.changePage}
                    hideNavigation={true}
                    hideFirstLastPages={true}
                    innerClass = "pagination pagination-lg mt-3 justify-content-center"
                    itemClass = "page-item"
                    linkClass = "page-link"
                />

            </>
        );
    }
}


export default ListCours;