import classeService from '../../services/classeService';
import { getAppClasses, getAppClasse, getAppSpecialite } from '../reducers/appReducer';
import { getEnseignantClasses } from '../../services/enseignantService';
import { getUser} from '../reducers/userReducer';
import { isTeacher } from '../../utils/fonctions';

/**
 * 
 */
export function loadAppClasses(){
    return function(dispatch,getState){
        dispatch(appClassesLoading());
        const user = getUser(getState());
        if(user && user.levels){
            classeService.findAll(user.levels).then(({data})=>{
                dispatch(setAppClasses(data));
                dispatch(appClassesLoading(false));
            }).catch((e)=>{
                dispatch(appClassesLoading(false));
            })
        }
    }
}




export function loadAppClasse(codeClasse){
    return function(dispatch,getState){
        const classes = getAppClasses(getState());
        const classe = classes.find(({code})=>(codeClasse===code));
        // la classe n'est pas defini
        if(!classe){
            return;
        }

        let appClasse;
        if(isTeacher(getUser(getState()).role)){
            appClasse = {...classe, specialites : getUser(getState()).specialites || []};
        }else{
            appClasse = {...classe};
        }
        dispatch(setAppClasse(appClasse));
        const specialite = getAppSpecialite(getState());
        if(specialite){
            const index = appClasse.specialites.findIndex((s)=>s.code===specialite.code);
            if(index===-1){
                dispatch(setAppSpecialite());
            }
        }
    }
}

export function setProgramme(programme){
    if(!programme){
        return;
    }
    return function(dispatch){
        dispatch(loadAppClasse(programme.classe.code));
        dispatch(setAppSpecialite(programme.specialite.code))
    }
}

///
function appClassesLoading(loading=true){
    return {type:"APP_CLASSES_LOADING",loading}
}

export function setAppPage(page=''){
    return {type:"SET_APP_PAGE",page}
}

export function setAppClasses(classes=[]){
    return {type:"SET_APP_CLASSES",classes}
}

export function setAppClasse(classe=''){
    return {type:"SET_APP_CLASSE",classe}
}

export function setAppSpecialite(codeSpecialite=null){
    return function(dispatch,getState){
        const classe = getAppClasse(getState());
        // la classe n'est pas defini
        if(!classe){
            return;
        }
        let specialite = classe.specialites.find((s)=>(s.code===codeSpecialite)) ;
        dispatch({type:"SET_APP_SPECIALITE",specialite});
    }
}


