import {connect} from 'react-redux';

//import {withRouter} from 'react-router-dom';

import {getAppClasse, getAppClasses, getAppSpecialite} from '../../redux/reducers/appReducer';
import {chapitresRecents,examensRecents, getLoadingChapitresRecents, getLoadingExamensRecents} from '../../redux/reducers/recentReducer';

import Home from './Home';
import { loadAppClasse, setAppSpecialite } from '../../redux/actions/appActions';
import { loadExamensRecents, loadChapitresRecents } from '../../redux/actions/recentsActions';


function mapStateToProps(state) {
    return {
        classe : getAppClasse(state),
        specialite : getAppSpecialite(state),
        classes : getAppClasses(state),
        chapitresRecents : chapitresRecents(state),
        examensRecents : examensRecents(state),
        loadingChapitres : getLoadingChapitresRecents(state),
        loadingExamens : getLoadingExamensRecents(state)
    };
}

function mapDispatchToProps(dispatch) {
    return {
        loadAppClasse : function(classeID){
            dispatch(loadAppClasse(classeID));
        },
        loadExamensRecents : function(){
            dispatch(loadExamensRecents());
        },
        loadChapitresRecents : function(){
            dispatch(loadChapitresRecents());
        },
        setAppSpecialite : function(specialite){
            dispatch(setAppSpecialite(specialite));
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Home);
