const initialState = {
    
    chapitres : [],
    chapitrePageNumber : 1,
    chapitrePageTotal : 0,
    loadingChapitres : false,

    classes : [],
    classe : null,
    etat : null,

    chapitre : {},
    loadingChapitre : false,

    isOpenChapitreModal : false
}

export default function reducer(state = initialState, action = {}) {

    switch (action.type) {

        // List chapitres
        case "SET_CHAPITRES":
            return {...state, chapitres : action.chapitres };
        case "SET_CHAPITRE_PAGE_NUMBER":
            return {...state, chapitrePageNumber : action.pageNumber };
        case "SET_CHAPITRE_PAGE_TOTAL":
            return {...state, chapitrePageTotal : action.pageTotal };
        
        case "LOADING_CHAPITRES":
            return {...state, loadingChapitres : action.loading };

        /// Filtre 
        case "SET_COURS_CLASSES" : 
            return {...state, classes : action.classes}
        case "SET_COURS_CLASSE" : 
            return {...state, classe : action.classe}
        case "SET_COURS_ETAT" : 
            return {...state, etat : action.etat}

        // Chapitre
        case "SET_CHAPITRE":
            return {...state, chapitre : action.chapitre};
        case "LOADING_CHAPITRE":
            return {...state, loadingChapitre : action.loading };

        case "TOGGLE_CHAPITRE_MODAL":
            return {...state, isOpenChapitreModal : !state.isOpenChapitreModal}

        default:
            return state;
    }
}

export const getListChapitres = ({chapitreReducer})=>(chapitreReducer.chapitres);
export const getChapitrePageNumber = ({chapitreReducer})=>(chapitreReducer.chapitrePageNumber);
export const getChapitrePageTotal = ({chapitreReducer})=>(chapitreReducer.chapitrePageTotal);
export const isLoadingChapitres = ({chapitreReducer})=>(chapitreReducer.loadingChapitres);

export const getCoursClasses = ({chapitreReducer})=>(chapitreReducer.classes);
export const getCoursEtat = ({chapitreReducer})=>(chapitreReducer.etat);
export const getCoursClasse = ({chapitreReducer})=>(chapitreReducer.classe);

export const getFullChapitre = ({chapitreReducer})=>(chapitreReducer.chapitre);
export const isLoadingChapitre = ({chapitreReducer})=>(chapitreReducer.loadingChapitre);

export const isOpenChapitreModal = ({chapitreReducer})=>(chapitreReducer.isOpenChapitreModal);
