import { CODE_COURS, CODE_EXERCICES } from "../routes/liens";

//import history from '../history';



/**
 * 
 * @param {*} chapitreID : 
 * @param {*} p 
 */
export function lienOuvrirChapitre({chapitreID,p}){
    const link = `/${CODE_COURS}/lecture/${chapitreID}`;
    return p ? `${link}?p=${p}` : link;
}

/**
 * 
 * @param {*} chapitreID : 
 */
export function lienGestionChapitre(chapitreID=null){
    const link = `/${CODE_COURS}/gestion`;
    return chapitreID ? `${link}/${chapitreID}` : link;
}

/**
 * 
 * @param {*} chapitreID : 
 */
export function lienGestionChapitreContent(chapitreID=0){
    return `/${CODE_COURS}/gestion/${chapitreID}/modifier`;
}


export function lienCreerExercice(chapitreID){
    return `/${CODE_EXERCICES}/${chapitreID}/creer`;
}

export function lienModifierEnonce(exerciceID){
    return `/${CODE_EXERCICES}/${exerciceID}/modifier?p=e`;
}

export function lienModifierCorrection(exerciceID){
    return `/${CODE_EXERCICES}/${exerciceID}/modifier?p=c`;
}