import React from 'react';
import {Link} from 'react-router-dom';
import classnames from 'classnames';
import qs from 'qs';

import Loading from '../../../commun/Loading';

import { toLink, getLienEnseignement } from '../../../navigation';

import { CODE_COURS } from '../../../routes/liens';

import Chapitre from './Chapitre';
import Presentation from '../../../commun/Presentation';
import TimeLine from '../../../commun/TimeLine';

//import ListExercice from '../../Exercices/ListExercice';
import { lienOuvrirChapitre, lienGestionChapitre } from '../../../navigation/cours';
import MenuRetour from '../../../commun/Retour';
import ProtectedButton from '../../../commun/ProtectedButton';

export const TAB_QUIZ = "q";
export const TAB_FICHE = "f";
export const TAB_EXERCICES = "e"

export const  COURS_TABS = [
    {
        name : "Le cours",
        p : TAB_FICHE,
        icon : require('../../../img/icon-fiche.png')
    },
    {
        name : "Quiz",
        p : TAB_QUIZ,
        icon : require('../../../img/icon-quiz.png')
    },
    {
        name : "Exercices",
        p : TAB_EXERCICES,
        icon : require('../../../img/icon-exercice.png')
    }
]


/**
 * Renvoie la page courante en fonction des params : La page des cours ou des exercices
 * @param {*} search 
 */
function getPage({search}){
    const {p} = qs.parse(search,{ ignoreQueryPrefix: true });
    for (let i = 0; i < COURS_TABS.length; i++) {
        if(COURS_TABS[i].p===p){
            return p
        }
    }
    return COURS_TABS[0].p;
}

class VoirCours extends React.Component  {

    constructor(props){
        super(props);
        this.state = {
            page : getPage(this.props.location),
            chapitreID : null
        }
    }

    componentDidMount(){
        window.scroll(0,0);
        const {exo} = qs.parse(this.props.location.search,{ ignoreQueryPrefix: true });
        this.props.loadCours(this.props.match.params.chapitreID,exo ? exo : 1,);
        this.setState({chapitreID : this.props.match.params.chapitreID});
    }


    static getDerivedStateFromProps(nextProps, prevState){
        const {chapitreID} = nextProps.match.params;
        if(prevState.chapitreID && chapitreID.toString() !== prevState.chapitreID){
            nextProps.loadCours(chapitreID);
            //window.scroll(0,0);
            return {chapitreID};
        }
        const p = getPage(nextProps.location);
        if(p!==prevState.page){
            return {page:p};
        }
        return null;
    }

    editerChapitre = ()=>{
        toLink(lienGestionChapitre(this.props.chapitre.id));
    }
    
    loadExercice = (exo)=>{
        const {id} = this.props.chapitre;
        const lien = lienOuvrirChapitre({chapitreID: id, p : this.state.page})
        toLink(lien+"&exo="+exo);
        this.props.loadExercices(id,exo);
    }

    renderCoursTab(){
        const {page} = this.state;
        switch (page) {
            case TAB_QUIZ:
                return(<div>Quiz</div>);
            case TAB_EXERCICES:
                return null       
            default: 
                return <Chapitre content={this.props.chapitre || {}} />
        }
    }

    render(){

        if(!this.props.chapitre.id){
            return(
                <div className="container">
                    <Loading />
                </div>
            )
        }
        
        /*let index  = this.props.chapitres.findIndex((item)=>(
            this.props.chapitre.id.toString()===item.id.toString()
        ));*/
            
        const {programme} = this.props.chapitre;
            
        return (
            
            <div className="enseignement-container">
                <MenuRetour 
                    title = {this.props.chapitre.title} 
                    link = {getLienEnseignement({codeClasse : programme.classe.code, codeSpecialite : programme.specialite.code, page : CODE_COURS})}
                />
                <div className="enseignement-container__main">
                    <div className="container">
                        <Presentation programme={programme} title = '' resume = {this.props.chapitre.resume} editer = {this.editerChapitre}>
                            
                            <div className="enseignement-menu">
                                <div className="enseignement-tab">
                                    {COURS_TABS.map((tab)=>(
                                        <Link key={`tab_${tab.p}`}
                                            to={lienOuvrirChapitre({chapitreID :this.props.chapitre.id, p : tab.p})} 
                                            className={classnames("enseignement-tab__item",{active:this.state.page===tab.p})}>
                                            <img alt="" className="enseignement-tab__icon" src={tab.icon} />
                                            <span className="enseignement-tab__name">{tab.name}</span>
                                        </Link>
                                    ))}
                                </div>
                            </div>
                            
                            <ProtectedButton
                                icon = "edit"
                                onClick={()=>{}} />
                        
                        </Presentation>

                        <div className="enseignement-content" >
                            
                            {/*<Paginate elements = {this.props.chapitres} index = {index} page={this.state.page} left />*/}
                            {this.renderCoursTab()}
                            {/*<Paginate  elements = {this.props.chapitres} index = {index} page={this.state.page}/>*/}
                        </div>
                    </div>
                </div>

            </div>
        );
    }

}


export default VoirCours;