//import update from 'react-addons-update';


const initialState = {
    classes : [],
    classe : {
        specialites : []
    },
    page : '',
    specialite : '',
    classeLoading: false,
    classesLoading: true
}

export default function reducer(state = initialState, action = {}) {

    switch (action.type) {

        case "SET_APP_CLASSES":
            return {...state, classes : action.classes};
        
        case "SET_APP_CLASSE":
            return {...state, classe : action.classe};

        case "APP_CLASSES_LOADING":
            return {...state, classesLoading : action.loading};

        case "APP_CLASSE_LOADING":
            return {...state, classeLoading : action.loading};

        case "SET_APP_SPECIALITE":
            return {...state, specialite : action.specialite};

        case "SET_APP_PAGE":
            return {...state, page : action.page};
                    
        default:
            return state;
    }
}

export const getAppPage = (state) => state.appReducer.page;
export const getAppSpecialite = (state) => state.appReducer.specialite;
export const getAppClasse = (state) => state.appReducer.classe;
export const getAppClasses = (state) => state.appReducer.classes;
export const appClassesLoading = (state) => state.appReducer.classesLoading;
export const appClasseLoading = (state) => state.appReducer.classeLoading;

export const getAppSpecialites = (state) => state.appReducer.specialites;
