import request from './index';

const BASE = "/classes";

function findAll(levels=[]) {
    let url = BASE;
    if(levels && levels.length !== 2){
        url = `${url}?level=${levels[0].id}`;
    }
    console.log(url)
    return request({url: url});
}

function find(classeID, withSpecialite=false) {
    const url = `${BASE}/${classeID}`;
    return request({
        url: withSpecialite ? `${url}?s=1` : url,
        method: "GET"
    })
}


export function saveClasse(classe) {
    let url = `${BASE}`;
    let method = "POST";
    const {id} = classe;
    
    if (id && id !== null && id !== "") {
        url = `${url}/${id}`;
        method = "PUT";
    }
    return request({
        url: url,
        data: {
            ...classe,
            id: null
        },
        method: method
    })
}

const classeServicce = {
    findAll,
    find
}

export default classeServicce;