import React from 'react'
import { Switch, Route } from 'react-router-dom';
import {connect} from 'react-redux';
//import EditerResume from '../../pages/Cours/EditerCours/EditerResume';
import { CODE_DEVOIRS } from '../routes/liens';
import { setAppPage } from '../redux/actions/appActions';
import ListDevoirs from '../pages/Devoirs/List';



class DevoirsLayout extends React.Component{
    componentDidMount(){
        this.props.setAppPage(CODE_DEVOIRS);
    }
    render(){
        return(
            <Switch>
                <Route exact path={`${this.props.match.url}`} component={ListDevoirs} />
            </Switch>
        );
    }
}

function mapStateToProps(state) {
    return {};
}

function mapDispatchToProps(dispatch) {
    return {
        setAppPage : function(page){
            dispatch(setAppPage(page));
        }
    }
}

export default connect(mapStateToProps,mapDispatchToProps)(DevoirsLayout);