import React from 'react';
import TextRender from '../../../../commun/TextRender';
import EnseignementAside from './EnseignementAside';
import ProtectedButton from '../../../../commun/ProtectedButton';
import { toLink } from '../../../../navigation';

import { lienGestionChapitreContent } from '../../../../navigation/cours';

class Chapitre extends React.Component  {

    editerContent = ()=>{
        toLink(lienGestionChapitreContent(this.props.chapitre.id));
    }

    /**
     * Rendu de la page HTML
     */
    render(){
        const {contents} = this.props.chapitre;
        return (
            <React.Fragment>
                <div id="enseignement-content__main" className="enseignement-content__main">
                    
                    <div className="enseignement">
                        <ProtectedButton
                            className = "enseignement-edit__right"
                            onClick = {this.editerContent} 
                        />
                        <TextRender html={false} text={this.props.chapitre.content || ''} />
                    </div>
                </div>
                {contents && contents.length !== 0 &&
                    <div id="enseignement-content__aside" className="enseignement-content__aside">
                        <EnseignementAside label = "Table des matires">
                            {contents.map((content,i)=>(
                                <div key={"c"+content.id+i}>
                                    <a className="principal" href={`#${content.id}`}>
                                        {content.titre}
                                    </a>
                                    {content.subSections.map((sub,i)=>(
                                        <a key={sub.titre+i} className="sub" href={`#${sub.id}`}>
                                            {sub.titre}
                                        </a>
                                    ))}
                                </div>
                            ))}
                        </EnseignementAside>
                    </div>
                }
            </React.Fragment> 
        );
    }

}

Chapitre.default = {
    chapitre : {}
}

export default Chapitre;