import React from 'react';

import ProtectedButton from './ProtectedButton';
import { formatProgramme } from '../utils/fonctions';

class Presentation extends React.Component{

    render(){
        const {title,resume,programme} = this.props;
        return(
            <div className={"enseignement-presentation "+this.props.className}>
                <p className="resume">{resume}</p>
                <p className="programme">{formatProgramme(programme)}</p>
                <div className="d-flex justify-content-between align-items-center">
                    {this.props.children}
                </div>
            </div>
        )
    }
}

Presentation.default = {
    title : '',
    resume : '',
    className : '',
    page : ''
}

export default Presentation;