import React from 'react';
import EnseignementMenu from '../../../commun/EnseignementMenu';
import MenuItem from '../../../commun/EnseignementMenu/MenuItem';
import ClassDropdown from '../../../commun/ClassDropdown';
import Filtre from '../../../commun/Filtre';

class ListExamens extends React.Component  {

    /**
     * Rendu de la page HTML
     */
    render(){
        return (
            <>
                <EnseignementMenu>
                    <MenuItem>
                        <ClassDropdown />
                        <Filtre />
                        <h3 className="enseignement-menu__text">7 sujets d'examens trouvs</h3>
                    </MenuItem>
                    <MenuItem right>
                        <div className="shadow-sm btn btn-vert">
                            Ajouter un examen
                        </div>
                    </MenuItem>
                </EnseignementMenu>
            </>
        );
    }

}


export default ListExamens;