import axios from 'axios';
import { createAuthHeader } from '../utils/token';
//import store from '../redux/store';
//import { setMessage } from '../redux/actions/errorActions';
import {formatErrors} from '../utils/errorUtil';

const baseURL = process.env.NODE_ENV === 'production' ? "https://lonnya.000webhostapp.com/api" : "http://localhost:8000/api";

const client = axios.create({
    baseURL: baseURL,
    headers : {
        'Content-Type' : 'application/json',
        'Accept': 'application/json',
        //'Access-Control-Allow-Origin':'*'
    }
})

const request = function(options = {}, auth=true){
    const onSuccess = function(response){
        return response.data;
    }
    const onError = function(error){
        const {response} = error;
        if(response && response.status===422){
            return Promise.reject(formatErrors(response.data.errors));
        }
        console.log(error.response);
        return Promise.reject(error);
    }
    if(!options.method){
        options.method = "GET";
    }
    if(auth===true){
        options.headers = createAuthHeader();
    }    
    return client(options)
            .then(onSuccess)
            .catch(onError);
}

export default request;