import React from 'react'
import { Switch, Route } from 'react-router-dom';
import {connect} from 'react-redux';
//import EditerResume from '../../pages/Cours/EditerCours/EditerResume';
import VoirCours from '../pages/Cours/VoirCours';
import ListCours from '../pages/Cours/List';
import { CODE_COURS } from '../routes/liens';
import { setAppPage } from '../redux/actions/appActions';



class CoursLayout extends React.Component{
    componentDidMount(){
        this.props.setAppPage(CODE_COURS);
    }
    render(){
        return(
            <>
                <Switch>
                    <Route exact path={`${this.props.match.url}`} component={ListCours} />
                    <Route exact path={`${this.props.match.url}/lecture/:chapitreID`} component={VoirCours} />
                </Switch>
            </>
        );
    }
}

function mapStateToProps(state) {
    return {};
}

function mapDispatchToProps(dispatch) {
    return {
        setAppPage : function(page){
            dispatch(setAppPage(page));
        }
    }
}

export default connect(mapStateToProps,mapDispatchToProps)(CoursLayout);