import request from './index';

const BASE_CONTENT = "/contents";


function get(contentID) {
    return request({
        url: `${BASE_CONTENT}/${contentID}`,
    });
}

export function save(content) {
    let formData = new FormData();
    
    formData.append('id', content.id);
    formData.append('data', content.data);
    formData.append('contentable', content.contentable);
    formData.append('etat', content.etat);

    return request({
        url: BASE_CONTENT,
        data: formData,
        method: "POST"
    },true)
}

const contentService = {get,save};

export default contentService;