import React, { Component } from 'react';

import {latexRender} from '../utils/latex';
import marked from 'marked-katex';

class TextToLatex extends Component {

    latexRender(text){
        try {
            const {result, errors}  = latexRender(!this.props.isHtml ? marked(text) : text);
            this.errors = errors
            return result;
        } catch (error) {
            console.log(error)
            return marked(text);
        }
    }
    

    render() {
        const {text} = this.props;
        if(!text || text === null){
            return null;
        }
        return (
            <div dangerouslySetInnerHTML={{__html : this.latexRender(text, true)}} />
        );
    }
}

TextToLatex.defaultProps = {
    isHtml : true
}

export default TextToLatex;