import React from 'react';

class EnseignementMenuItem extends React.Component {
    render() {
        const positionClass = this.props.right ? "float-right" : 'float-left';
        return (
            <div className={`enseignement-menu__filtre ${positionClass}`}>
                {this.props.children}
            </div>
        )
    }
}


// Exporte le composant
export default EnseignementMenuItem;