import React from 'react';
import {Switch, Route} from 'react-router-dom';
import AppHeader from '../pages/Header';
import { CODE_LIVRES, CODE_COURS, CODE_EXERCICES, CODE_DEVOIRS, CODE_EXAMENS } from '../routes/liens.js';
import CoursLayout from './CoursLayout.js';
import LivresLayout from './LivresLayout.js';
import ExercicesLayout from './ExercicesLayout.js';
import DevoirsLayout from './DevoirsLayout.js';
import ExamenLayout from './ExamenLayout.js';


class EnseignementLayout extends React.Component {
        
    render() {
        return (
            <div className="enseignement-layout">
                <AppHeader />
                <Switch>
                    <Route path = {`/${CODE_LIVRES}`} component = {LivresLayout} />
                    <Route path = {`/${CODE_COURS}`} component = {CoursLayout} />
                    <Route path = {`/${CODE_EXERCICES}`} component = {ExercicesLayout} />
                    <Route path = {`/${CODE_DEVOIRS}`} component = {DevoirsLayout} />
                    <Route path = {`/${CODE_EXAMENS}`} component = {ExamenLayout} />
                </Switch>
            </div>
        )
    }
}

// Exporte le composant
export default EnseignementLayout;