import {connect} from 'react-redux';

import VoirCours from './VoirCours.jsx';

import { loadChapitre, loadExercices } from '../../../redux/actions/chapitresActions.js';

import {getFullChapitre,isLoadingChapitre, getListChapitres} from '../../../redux/reducers/chapitreReducer';
import { getChapitreExercices, getChapitreExoPage, getChapitreExoTotal, loadingExercices } from '../../../redux/reducers/exerciceReducer.js';


function mapStateToProps(state) {
    return {
        chapitre : getFullChapitre(state),
        chapitres : getListChapitres(state),
        isLoadingChapitre : isLoadingChapitre(state),

        exercices : getChapitreExercices(state),
        page : getChapitreExoPage(state),
        total : getChapitreExoTotal(state),
        loadingExercices : loadingExercices(state)
    };
}

function mapDispatchToProps(dispatch) {
    return {
        loadCours: function(chapitreID,pageExo=1){
            dispatch(loadChapitre(chapitreID));
            dispatch(loadExercices(chapitreID,pageExo));
        },
        loadExercices : function(chapitreID,pageExo){
            dispatch(loadExercices(chapitreID,pageExo));
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(VoirCours);
