import katex from 'katex';
import marked from 'marked-katex';

const regexpr = /\$\$[\s\S]+?\$\$|\$[\s\S]+?\$/g;

export  function checkLatex(text){
    const errors = [];
    if(text && text !== null && text!==""){
        const textWithoutHtml = text.replace(/(<([^>]+)>)/gi, "");
        const latexParts = textWithoutHtml.match(regexpr);
        if(latexParts){
            latexParts.forEach(item => {
                let t = item[1]==='$' ? item.slice(2,-2) : item.slice(1,-1);
                try{
                    katex.renderToString(t);
                } catch ({message, position}) {
                    errors.push(message);
                }
            });
        }
        
    }
    return errors;
}



export function latexRender(text){
    const errors = [];
    const latexParts = text.match(regexpr);
    if(latexParts){
        let result = text;
        latexParts.forEach(item => {
            let t = item[1]==='$' ? item.slice(2,-2) : item.slice(1,-1);

            try{
                if(item[1]==='$'){
                    result = result.replace(item , '<div>'+katex.renderToString(t)+'</div>');
                }else{
                    result = result.replace(item , katex.renderToString(t));
                }
            } catch ({message, position}) {
                errors.push(message);
            }
        });
        return {result, errors};
    }
    return {result : text, errors};
}


export function latextAndMardownRender(text){
    try {
        const {result, errors}  = latexRender(marked(text));
        return result;
    } catch (error) {
        console.log(error)
        return marked(text);
    }
}