/**
 * 
 */
export function loadBooks(){
    return function(dispatch,getState){
        dispatch(setLoading());
        const {classe,pageNumber,etat} = getState().bookReducer;
        const {user} = getState().userReducer;
        bookService.getAll(user.username,classe,etat,pageNumber).then(({data,meta})=>{
            dispatch(setBooks(data));
            dispatch(setPageTotal(meta.total));
            dispatch(setLoading(false));
        })
        .catch((error)=>{
            dispatch(setLoading(false));
            console.log(error);
        })
    }
}

/**
 * 
 */
export function loadBook(bookID){
    return function(dispatch){
        dispatch(setLoading());
        bookService.getOne(bookID).then(({data})=>{
            const {contents, contentHtml} = getTableMatieres(data.content.data);
            data.plan = contents;
            data.content.data = contentHtml;
            dispatch(setBook(data));
            dispatch(setLoading(false));
        })
        .catch((error)=>{
            dispatch(setLoading(false));
            console.log(error);
        })
    }
}

//
function setLoading(loading=true){
    return {type : "LOADING_BOOKS", loading};
}
function setBook(book={}){
    return {type : "SET_BOOK",book};
}
function setBooks(books=[]){
    return {type : "SET_BOOKS",books};
}

function setPageTotal(pageTotal=0){
    return {type : "SET_BOOKS_PAGE_TOTAL",pageTotal};
}


// Filtre 
function setClasses(classes=[]){
    return {type : "SET_BOOKS_CLASSES",classes};
}
export function setBooksEtat(etat=null){
    return {type : "SET_BOOKS_ETAT",etat};
}
export function setBooksClasse(classe=null){
    return {type : "SET_BOOKS_CLASSE",classe};
}
