import React from 'react';
import { ButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';

class Filtre extends React.Component {
    constructor(props) {
    super(props);

        this.toggle = this.toggle.bind(this);
        this.state = {
            show: false
        };
    }

    toggle() {
        this.setState({
            show: !this.state.show
        });
    }

    onClick = (e)=>{
        if(this.props.onClick){
            this.props.onClick(e.target.value || null);
        }
    }

    render() {
        const {etat} = this.props;
        let filtre = null;
        if(etat==="1"){
            filtre = "Publi";
        }else if(etat==="0"){
            filtre = "Non publi";
        }
        return (
            <>
                <ButtonDropdown isOpen={this.state.show} toggle={this.toggle}>
                    <DropdownToggle color="outline-secondary" caret>
                        Filtre
                    </DropdownToggle>
                    <DropdownMenu>
                        {this.props.etat && 
                            <DropdownItem value={null} onClick={this.onClick}>
                                Tous
                            </DropdownItem>
                        }
                        <DropdownItem value = "1"  onClick={this.onClick}>
                            Publi
                        </DropdownItem>
                        <DropdownItem value="0"  onClick={this.onClick}>
                            Non publi
                        </DropdownItem>
                    </DropdownMenu>
                </ButtonDropdown>
                {filtre && <span className="filtre-label"> {filtre} </span>}
            </>
        );
    }
}

export default Filtre;