import history from '../history';
import { HOME, ENSEIGNEMENT_BASE } from '../routes/liens';

export function toLink(link='/'){
    history.push(link)
}

export function toHome(){
    history.push(HOME);
}

export function getLienEnseignement({codeClasse, codeSpecialite, page, pageNumber}){
    let link = `${ENSEIGNEMENT_BASE}/${codeClasse}`;
    if(codeSpecialite){
        link = `${link}/${codeSpecialite}`;
    }
    if(page){
        link = `${link}?p=${page}`;
    }
    if(pageNumber){
        const s = page ? '&' : '?';
        link = `${link}${s}num=${pageNumber}`;
    }
    return link;
}

export function lecture(code,id){
    return `/${code}/lecture/${id}`;
}


export function editerContent(code,id){
    return `/${code}/content/${id}`;
}

export function editer(code,id){
    let url = `/${code}/editer`;
    return id ? `${url}?key=${id}` : url;
}

export function toLienEnseignement({codeClasse, codeSpecialite, page,pageNumber}){
    toLink(getLienEnseignement({codeClasse,codeSpecialite,page,pageNumber}))
}