import React from 'react';
import {Link} from 'react-router-dom';
import classnames from 'classnames';
import { HOME } from '../../routes/liens';
import { ENSEIGNEMENTS } from '../../utils/constants';


class AppHeader extends React.Component{

    handleSlider = ()=>{
        var element = document.getElementById("slider");
        const className = 'slider-show';
        if (element.classList) {
            element.classList.toggle(className);
        } else {
        // For IE9
        var classes = element.className.split(" ");
        var i = classes.indexOf(className);

        if (i >= 0)
            classes.splice(i, 1);
        else
            classes.push(className);
            element.className = classes.join(" ");
        }
    }

    render(){
        const navItems = ENSEIGNEMENTS.map(({code,name})=>(
            <Link to={`/${code}`} key={code} className={classnames("header-nav__item",{"active" : code===this.props.page})}>
                {name}
            </Link>
        ))
        return(
            <>
                <div className="header-fluid">
                    <header className="header-content">
                        <div className="header-left">
                            <Link to={HOME} className="header-logo">
                                <img  src={require('../../img/logo.png')} alt="Lonnya" />
                            </Link>
                            <div className="header-nav">
                                {navItems}
                            </div>
                        </div>
                        <div className="header-right">
                            <div className="header-profil">
                                <img src={require('../../img/person-icon.png')}  alt="logo"/>
                            </div>
                            <div onClick={this.handleSlider} className="header-menu">
                                <img src={require('../../img/menu.png')}  alt="logo"/>
                            </div>
                        </div>
                    </header>
                </div>
                <div id="slider" className="slider-container" >
                    <div className="slider-content">
                        <div onClick={this.handleSlider} className="slider-left shadow-sm">
                            <div className="slider-profil">
                                <img className="slider-profil__image" src={require('../../img/person-image.png')}  alt="logo"/>
                            </div>
                            <div className="header-nav">
                                <Link to={"/"} className={classnames("header-nav__item",{"active" : "ok"==="cours"})}>
                                    Mon compte
                                </Link>
                                {navItems}
                            </div>
                        </div>
                        <div className="slider-right" onClick={this.handleSlider} />
                    </div>
                </div>
            </>
        );
    }
    
}



export default AppHeader;