import React, {Component} from 'react';

//import {Link} from 'react-router-dom';

import {
    Navbar,
    Nav,
} from 'reactstrap';

import {ScrollSync, ScrollSyncPane} from 'react-scroll-sync';

import TextareaAutosize from 'react-autosize-textarea';

import TextToLatex from './TextRender';
//import Loading from './Loading';


//import SimpleMDE from 'react-simplemde-editor';
import "simplemde/dist/simplemde.min.css";
//import { latextAndMardownRender } from '../utils/latex';

//import CodeMirror from 'react-codemirror';
import contentService from '../services/contentService';
import { lecture, toLink } from '../navigation';
require('codemirror/mode/markdown/markdown');

export default class Editeur extends Component {
    
    constructor(props){
        super(props);
        this.state = {
            data : '',
            id : null,
            contentable : null,
        }
    }

    handleData = (e)=>{
        this.setState({
            data : e.target.value
        })
    }

    componentDidMount(){
        contentService.get(this.props.match.params.contentID).then(({data,id, contentable})=>{
            this.setState({data, id, contentable})
        }).catch((e)=>{
            console.log(e)
        })
    }

    back = ()=>{
        toLink(lecture(this.props.code, this.state.contentable));
    }

    onSave = ()=>{
        contentService.save({...this.state}).then((data)=>{
            this.back();
        }).catch((e)=>{
            console.log(e)
        })
    }

    render() {
        return (
            <div className="containesr-fluid">
                <div className="header-fluid">
                    <header className="header-content">
                        <div className="header-left">
                            <div className="header-logo">
                                <img  src={require('../img/logo.png')} alt="Lonnya" />
                            </div>
                        </div>
                        <div className="header-right">
                            <div onClick = {this.onSave} className="btn btn-vert">
                                Enregistrer
                            </div>
                            <div onClick = {this.back} className="btn btn-rouge ml-2">
                                Quitter
                            </div>
                        </div>
                    </header>
                </div>
                
                <ScrollSync>
                    <div className="row">
                        <ScrollSyncPane>
                            <div className="col-sm-6 col-md-6 fixarear" style={{backgroundColor: "#fff"}}>
                                <TextareaAutosize
                                    id="content"
                                    name="content"
                                    rows={5}
                                    placeholder="Saisir votre texte ..."
                                    className="form-control col-12"
                                    style={{backgroundColor: 'transparent', border:0,borderRadius:0, boxShadow:"none"}}
                                    value={this.state.data}
                                    disabled={this.props.isLoading}
                                    onChange={this.handleData}
                                />
                            </div>
                        </ScrollSyncPane>
                        <ScrollSyncPane>
                            <div className="col-sm-6 p-2 col-md-6 fixarear offset-md-6 offset-sm-6 d-none d-sm-block enseignement">
                                <TextToLatex isHtml={false} text={this.state.data} />
                            </div> 
                        </ScrollSyncPane>
                    </div>
                </ScrollSync>
            </div>
        );
    }
}